import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import Card from 'components/UI/Card/Card';
import Heading from 'components/UI/Heading/Heading';
import Text from 'components/UI/Text/Text';
import TextLink from 'components/UI/TextLink/TextLink';
import RenderReservationForm from './RenderReservationForm';

const CardHeader = ({ price, priceStyle, pricePeriodStyle, linkStyle }) => {
  return (
    <Fragment>
      <Heading
        content={
          <Fragment>
            ${ price } <Text as="span" content="/ day" {...pricePeriodStyle} />
          </Fragment>
        }
        {...priceStyle}
      />
      <a href="https://turo.com/us/en/drivers/9170089" target="blank" > Contact Owner </a>
    </Fragment>
  );
};

export default function Reservation(props) {

  const { collectionId, price, account } = props;

  return (
    <Card
      className="reservation_sidebar"
      header={<CardHeader price={ price } />}
      content={<RenderReservationForm collectionId={ collectionId } account={account}/>}
      footer={
        <p>
          You can mint up to 3 NFTs at one time. <br /> <TextLink to="/#1" content="See details" />
        </p>
      }
    />
  );
}

CardHeader.propTypes = {
  priceStyle: PropTypes.object,
  pricePeriodStyle: PropTypes.object,
  linkStyle: PropTypes.object,
};

CardHeader.defaultProps = {
  priceStyle: {
    color: '#2C2C2C',
    fontSize: '25px',
    fontWeight: '700',
  },
  pricePeriodStyle: {
    fontSize: '15px',
    fontWeight: '400',
  },
  linkStyle: {
    fontSize: '15px',
    fontWeight: '700',
    color: '#008489',
  },
};
